import { Component, OnInit } from '@angular/core';
import { UserRegistrationService } from '../user-registration.service';
import { HttpClient } from '@angular/common/http';
import { User } from './user';
import { Observable, Subject } from 'rxjs';

@Component({
  selector: 'app-login-component',
  templateUrl: './login-component.component.html',
  styleUrls: ['./login-component.component.css']
})
export class LoginComponentComponent implements OnInit {

  email="";
  password="";

  Email="";
  Password="";
  Fname="";

  Userr={};
  
  
  constructor(private UserSer:UserRegistrationService) { 
    
  }

  login(email:string,password:string)
  {
    console.log("btn click");
    this.Email=this.email;
    this.Password=this.password;
    let response=this.UserSer.checklogin(this.Email,this.Password);
    response.subscribe(
      data=>{
        this.Userr=data;
        console.log(this.Userr);
      } 
    )
  }
  sendMsg(Fname:string)
  {
    alert(Fname);
    if(this.Fname!=null)
    {
      console.log(Fname);
      this.UserSer.sendFname(Fname)
      
    }
  }
 
  ngOnInit() {
   alert('After Login Successfully Click Next Button')
  }

}
