import { Component, OnInit } from '@angular/core';
import { UserRegistrationService } from '../user-registration.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-printers',
  templateUrl: './printers.component.html',
  styleUrls: ['./printers.component.css']
})
export class PrintersComponent implements OnInit {

  Price="";
  Title="";
  Desc="";
 
  constructor(private userReg:UserRegistrationService) { }

 
   

  ngOnInit() {

  }

OnSubmit(Forms:NgForm)
{

}
selected:File;
imglink="";
ImgLink="";
onselect(event)
  {
    this.selected=event.target.files[0];
  }
sm(title:string,price:string,desc:string)
{
  this.userReg.imgurl(this.selected).subscribe(
    data=>{
      const img=data.data.link
      this.ImgLink=img.toString();
      console.log(img);
      
      this.Title=title;
      this.Price=price;
      this.Desc=desc;
      this.imglink=this.ImgLink;
    
      let response=this.userReg.addPrinter(this.Title,this.Price,this.Desc,this.imglink);
      response.subscribe();
    }
    ,error=>{}
  )
}

}
