import { Component, OnInit } from '@angular/core';
import { UserRegistrationService } from '../user-registration.service';

@Component({
  selector: 'app-buy',
  templateUrl: './buy.component.html',
  styleUrls: ['./buy.component.css']
})
export class BuyComponent implements OnInit {
  Fname="";
  Mobilearray : any;
  Laptoparray : any;
  Ledarray : any;
  Printerarray : any;
  Cameraarray : any;


  constructor(private UserSer:UserRegistrationService) { }

  public desc(Img:string,title:string,price:string,desc:string,brand?:string)
  {
    this.UserSer.sendDesc(Img,title,desc,price,brand);
  }

  ngOnInit() {
    this.UserSer.msg$.subscribe
    (
      msgg=>(
        console.log("msg recieved"+msgg),
        this.Fname=msgg
      )
    )

    this.UserSer.getAllMobiles().subscribe(
      data=>{
        this.Mobilearray=data;
        console.log(this.Mobilearray);
      }
    )

    this.UserSer.getAllLaptop().subscribe(
      data=>{
        this.Laptoparray=data;
        console.log(this.Laptoparray);
      }
    )

    this.UserSer.getAllLed().subscribe(
      data=>{
        this.Ledarray=data;
        console.log(this.Ledarray);
      }
    )

    this.UserSer.getAllPrinter().subscribe(
      data=>{
        this.Printerarray=data;
        console.log(this.Printerarray);
      }
    )

    this.UserSer.getAllCamera().subscribe(
      data=>{
        this.Cameraarray=data;
        console.log(this.Cameraarray);
      }
    )
  }

}
