import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { UserRegistrationService } from '../user-registration.service';

@Component({
  selector: 'app-laptop',
  templateUrl: './laptop.component.html',
  styleUrls: ['./laptop.component.css']
})
export class LaptopComponent implements OnInit {

  
  brand='';
  Brand="";
  Price="";
  Title="";
  Desc="";

  ImgLink="";
  imglink="";
  selected:File;
 
  constructor(private userReg:UserRegistrationService) { }

 
   

  ngOnInit() {

  }

OnSubmit(Forms:NgForm)
{

}
onselect(event)
{
  this.selected=event.target.files[0];
}
sm(Forms:NgForm,brand:string,title:string,price:string,desc:string)
  {
    this.userReg.imgurl(this.selected).subscribe(
      data=>{
        const img=data.data.link
        this.ImgLink=img.toString();
        console.log(img);
        this.Brand=brand;
        this.Title=title;
        this.Price=price;
        this.Desc=desc;
        this.imglink=this.ImgLink;
      
        let response=this.userReg.addLaptop(this.Brand,this.Title,this.Price,this.Desc,this.imglink);
        response.subscribe();
      }
      ,error=>{}
    )


 


}
}

